<template>
  <div class="Scan">
    <div class="popup_title">
      <div class="popup_title_content">微信关注</div>
      <router-link class="popup_title_x" :to="{ name: pushName}">完成</router-link>
    </div>
    <div class="qrCode">
      <p>请用户微信扫描下方二维码，绑定手机即刻领取电子合同凭证。</p>
      <div class="img">
        <img :src="qrCode"/>
      </div>
    </div>
    <div class="mt_15 w_50" @click="goToPageDraw(orderId)" v-if="isDraw">
      <mt-button size="large" type="primary">抽奖</mt-button>
    </div>
  </div>
</template>
<script>
  import api from '../../api/index2'
  import { loadData, defaultPreHandler } from '../../common/utils'

  export default {
    name: 'Scan',
    data () {
      return {
        pushName: this.$route.meta.pushName,
        qrCode: null,
        orderId: null,
        isDraw: false,
        partnerId: null
      }
    },
    async created () {
      this.orderId = this.$route.query.orderId
      await this.getOrderDetail(this.orderId)
    },
    beforeRouteLeave (to, from, next) {
      window.history.pushState({}, 'title', '#/login')
      if (to.name === 'Draw') {
        window.history.pushState({}, 'title', '#/MyOrder')
        next()
      } else if (to.name !== 'Draw' && to.name !== 'MyOrder') {
        next('/MyOrder')
      } else {
        next()
      }
    },
    methods: {
      async getQrCode () {
        await loadData(() => {
          return api.getPartner(this.partnerId)
        }, scan => {
          this.qrCode = scan.qrCode
        }, defaultPreHandler, true)
      },
      async getOrderDetail (orderId) {
        await loadData(
          () => { return api.getOrderDetail(orderId) },
          async (orderDetail) => {
            this.partnerId = orderDetail.partnerId
            this.isDraw = orderDetail.awardStatus === 1
            await this.getQrCode()
          }, defaultPreHandler)
      },
      goToPageDraw (orderId) {
        this.$router.push({
          name: 'Draw',
          query: {
            orderId
          }
        })
      }
    }
  }
</script>
<style scoped>
  .w_50 {
    width: 50%;
    margin: auto;
  }

  .mt_15 {
    margin-top: 15px;
  }

  .qrCode {
    background: #fff;
  }

  .qrCode p {
    color: #888;
    font-size: 14px;
    line-height: 1.5rem;
    padding: 15px 15px 0;
    text-indent: 28px;
  }

  .qrCode .img {
    width: 50%;
    margin: auto;
    padding: 10px 0;
  }

  .qrCode .img img {
    width: 100%;
    text-align: center;
  }

  .popup_title {
    background: #26a2ff;
    font-size: 14px;
    color: #fff;
    width: 100%;
    height: 50px;
    border: 1px solid #f4f3f3;
    display: table;
    box-sizing: border-box;
    table-layout: auto;
    position: relative;
  }

  .popup_title_content {
    display: table-cell;
    vertical-align: middle;
    text-align: center;
    font-size: 16px;
  }

  .popup_title_x {
    color: #fff;
    text-decoration: none;
    position: absolute;
    top: 14px;
    right: 20px;
  }
</style>



